import React from 'react';
import './style.scss';

import id from './images/user.id.svg';
import days from './images/user.days.svg';
import cog from './images/cog.icon.svg';

import { setOptionsLicenseMenu, setCurrentLicense, setAutoRenewPersonal, setAutoRenewCorporate, setCurrentTab, setAdditionalScreenCorporate } from "../../../redux/licenses/licenses.actions";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';



const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});



const AdditionalTabForLicense = (props) => {

    let tabTitle, balanceString, daysLeft, configsLeft, currentLicense, balanceArray, autoRenew;
    let changeTo = '';
    let showLicenseMenu = props.licenses.options.menuForChangingLicense;

    let autoRenewHandler;

    switch (props.licenses.currentLicense) {
        case 'Personal':

            changeTo = 'Corporate';
            currentLicense = props.licenses.personalLicense;

            autoRenewHandler = (bool) => {
                props.setAutoRenewPersonal(bool);
            }

            break;

        case 'Corporate':

            changeTo = 'Personal';
            currentLicense = props.licenses.corporateLicense;
            autoRenewHandler = (bool) => {
                props.setAutoRenewCorporate(bool);
            }
            break;

        default:
            break;
    }

    balanceString = currentLicense.balance;
    tabTitle = currentLicense.title;
    daysLeft = currentLicense.daysLeft;
    configsLeft = currentLicense.configsLeft;
    balanceArray = balanceString.split('.');
    autoRenew = currentLicense.autoRenew;



    let menu = (<div id="changeLicenseMenu">
        <span onClick={() => {
            props.setCurrentTab('Activity');
            props.setAdditionalScreenCorporate(false);
            props.setCurrentLicense(changeTo);
            props.setOptionsLicenseMenu(false);
        }}>{ changeTo }</span>
    </div>);





    return (
        <div className="licenseTab">

            <div className="side-info">
                <h4 className="color-white">{tabTitle}</h4>

                <div>
                    <img src={id} alt="id"/><span>{props.currentUser.id}</span>
                </div>

                <div>
                    <img src={days} alt="days"/>
                    <span className="color-white">{ daysLeft } days <span className="color-grey">or</span> { configsLeft } configs</span>
                </div>

            </div>

            <div className="side">
                <div className="license__renew" >

                    <Switch checked={ autoRenew }
                            name="autoRenew"
                            onClick={()=>{autoRenewHandler(!autoRenew)}}
                    />

                    <span>Auto renew</span>

                    <div className="license__renew__btn"><img src={cog} alt="Cog"/></div>
                </div>

                <div className="license__balance" >
                    <svg width="12" height="21" viewBox="0 0 12 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M8.61719 9.99219L4.08594 8.66406C3.46094 8.50781 2.99219 8 2.875 7.375C2.71875 6.35938 3.5 5.5 4.47656 5.5H7.32812C7.95312 5.5 8.57812 5.69531 9.08594 6.08594C9.32031 6.24219 9.67188 6.20312 9.86719 6.00781L10.7656 5.10938C11.0391 4.83594 11 4.40625 10.7266 4.17188C9.75 3.42969 8.57812 3 7.32812 3H7.25V1.125C7.25 0.8125 6.9375 0.5 6.625 0.5H5.375C5.02344 0.5 4.75 0.8125 4.75 1.125V3H4.63281C2.71875 3 0.921875 4.25 0.492188 6.125C-0.0546875 8.3125 1.27344 10.4609 3.34375 11.0469L7.875 12.375C8.5 12.5312 9.00781 13.0391 9.08594 13.6641C9.24219 14.6406 8.46094 15.5 7.52344 15.5H4.63281C4.00781 15.5 3.38281 15.3047 2.875 14.9531C2.64062 14.7969 2.28906 14.8359 2.09375 15.0312L1.19531 15.9297C0.921875 16.2031 0.960938 16.6328 1.23438 16.8672C2.21094 17.6094 3.38281 18 4.63281 18H4.75V19.875C4.75 20.2266 5.02344 20.5 5.375 20.5H6.625C6.9375 20.5 7.25 20.2266 7.25 19.875V18H7.32812C9.24219 18 11.0391 16.7891 11.4688 14.9141C12.0156 12.7266 10.6875 10.5781 8.61719 9.99219Z" fill="white" fillOpacity="0.6"/>
                    </svg>
                    <span>{ balanceArray[0] }</span>
                    <span className="afterpoint">.{ balanceArray[1] }</span>

                    <div className="add-button" ></div>
                </div>
                <div className="changeLicense-btn" onClick={() => props.setOptionsLicenseMenu( !showLicenseMenu ) }></div>

                {showLicenseMenu ? menu : ''}
            </div>

        </div>
    );
};

const mapDispatchToProps = (dispatch) => {
    return {
        setOptionsLicenseMenu: (prop) => dispatch(setOptionsLicenseMenu(prop)),
        setCurrentLicense: (prop) => dispatch(setCurrentLicense(prop)),
        setAutoRenewPersonal: (prop) => dispatch(setAutoRenewPersonal(prop)),
        setAutoRenewCorporate : (prop) => dispatch(setAutoRenewCorporate(prop)),
        setCurrentTab : (prop) => dispatch(setCurrentTab(prop)),
        setAdditionalScreenCorporate: (prop) => dispatch(setAdditionalScreenCorporate(prop))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AdditionalTabForLicense);