import React from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';
import DealsTable from './DealsTable/DealsTable'

const Deals = () => {

    let tabs = [
        {name : 'My Deals', link: '/deals'},
        {name : 'New Deal', link: '/new-deal'}
    ];

    // {name: 'Date Created', filterBy: 'date'}


    let thead = [
        'Deal Title', 'Status', 'Seller', 'Buyer', 'Amount', 'Deal ID', 'Date Created'
    ];

    let tbody = [
        ['Lorem Ipsum', 'Wait term confirmations', 'Ronaldo', 'Lionel Messi', '$1200', '45643218889', '3 Apr, 18:11:33'],
        ['Lorem Ipsum', 'Done', 'Ronaldo', 'Lionel Messi', '$2430', '2321312889', '2 Apr, 18:11:33'],
        ['Lorem Ipsum', 'Done', 'Ronaldo', 'Lionel Messi', '$100', '123213123', '1 Apr, 18:11:33'],
    ];

    return (
        <div id="dashboard-deals">
            <TabsManager data={tabs} />

            <DealsTable tHead={thead} tBody={tbody} />
        </div>
    );
};

export default Deals;