import React from 'react';
import './style.scss'
import {useHistory} from "react-router-dom";

import AdditionalTabForLicense from './PersonalLicense/PersonalLicense';
import {setCurrentLicense, setCurrentTab} from "../../redux/licenses/licenses.actions";
import {connect} from "react-redux";


const TabsManager = (props) => {

    let history = useHistory();
    let path = history.location.pathname;
    let tabs, tabsComponent;
    let additionalComponent = '';
    let tabItemClassName = 'tabItem';

    if (props.data) {
        tabs = props.data.map((tab, i) => {
            let tabItem;
            if (path === tab.link) {
                tabItemClassName = 'tabItem tabItem--active';
            } else {
                tabItemClassName = 'tabItem';
            }

            tabItem = (<div className={tabItemClassName} key={i.toString()} onClick={() => {
                props.setCurrentTab(tab.name);
                history.push(tab.link);
            }}>
                {tab.name}
            </div>);

            return tabItem;
        });
    }

    if (props.licenseComponent) {
        additionalComponent = (<div className="tabLicense">
            <AdditionalTabForLicense data={props.licenses} user={props.currentUser}/>
        </div>);
    }
    if (props.licenses.currentLicense === 'Corporate' && props.licenses.corporateLicense.additionalScreen === true) {

        let generateClass = 'tabItem';
        let inviteClass = 'tabItem';
        if (props.licenses.currentTab === 'Generate') {
            generateClass = 'tabItem tabItem--active';
        } else if (props.licenses.currentTab === 'Invite') {
            inviteClass = 'tabItem tabItem--active';
        }

        tabsComponent = (<div className="tabWrapper">
            <div className='tabItem__arrow' onClick={() => {
                props.setCurrentTab('Users');
            }}>
                <svg width="10" height="14" viewBox="0 0 10 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1.0625 6.5C0.78125 6.78125 0.78125 7.25 1.0625 7.53125L7.125 13.625C7.4375 13.9062 7.90625 13.9062 8.1875 13.625L8.90625 12.9062C9.1875 12.625 9.1875 12.1562 8.90625 11.8438L4.09375 7L8.90625 2.1875C9.1875 1.875 9.1875 1.40625 8.90625 1.125L8.1875 0.40625C7.90625 0.125 7.4375 0.125 7.125 0.40625L1.0625 6.5Z" fill="white" fill-opacity="0.6"/>
                </svg>
            </div>
            <div className={tabItemClassName} onClick={
                () => {
                    props.setCurrentTab('Generate');
                }}>
                Generate
            </div>
            <div className={tabItemClassName} onClick={
                () => {
                    props.setCurrentTab('Invite');
                }}>
                Invite
            </div>
        </div>);
    } else {
        tabsComponent = (<div className="tabWrapper">{tabs}</div>);
    }


    return (
        <div id="TabsManager">

            {additionalComponent}

            {tabsComponent}

        </div>
    );
};

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCurrentLicense: (string) => dispatch(setCurrentLicense(string)),
        setCurrentTab: (string) => dispatch(setCurrentTab(string))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TabsManager);