import React from 'react';
import Minus from "./minus.svg";
import Plus from "./plus.svg";

const ConfigLimits = (props) => {
    return (
        <div className="tableCell-configLimits">
            <div className="value">
                {props.dataValue.val}
            </div>
            <div className="days">
                {props.dataValue.max}
            </div>
            <div className="btn">
                <img src={Minus} alt=""/>
            </div>
            <div className="btn">
                <img src={Plus} alt=""/>
            </div>
        </div>
    );
};

export default ConfigLimits;