import React, {Fragment} from 'react';
import MaterialTable from 'material-table';
import './CorporateCost__styles.scss';
import {connect} from "react-redux";

const CorporateCost = () => {

    let prepearAmount = (amount, type) => {
        switch (type) {
            case 'loss':
                return (
                    <span className="td--red">{amount}</span>)
                break;
            case 'profit':
                return (<span className="td--green">{amount}</span>)
                break;
            default:
                return amount
                break;
        }
    }

    const [state, setState] = React.useState({

        columns: [
            { title: 'Amount', field: 'amountValue', sorting: false, render: rowData => prepearAmount(rowData.amountValue , rowData.amountType) },
            { title: 'User', field: 'user', sorting: false },
            { title: 'Cost action', field: 'costAction',  sorting: false },
            { title: 'Cost detail', field: 'costDetail',  sorting: false },
            { title: 'Bought left', field: 'boughtLeft',  sorting: false },
            { title: 'Date', field: 'date',
                customSort: (a, b) =>  a.dateIndex - b.dateIndex }],

        data: [
            {
                dayTitle: 'Today',
                user: 'Lionel Messi',
                costAction: 'Buy Config',
                costDetail: 'Purchased config',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$991',
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                user: 'Lionel Messi',
                costAction: 'Buy Config',
                costDetail: 'Purchased config',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$991',
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                user: 'Lionel Messi',
                costAction: 'Buy Config',
                costDetail: 'Purchased config',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$993',
                date: '20 Apr, 19:12:33',
                dateIndex: 20200420191233
             },
            {
                user: 'Lionel Messi',
                costAction: 'Using Browser',
                costDetail: 'Pay per day using the browser',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$995',
                date: '20 Apr, 19:10:55',
                dateIndex: 20200420191055
            },
            {
                user: 'Lionel Messi',
                costAction: 'Buy Config',
                costDetail: 'Purchased config',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$997',
                date: '20 Feb, 13:13:55',
                dateIndex: 20200220131355
            },
            {
                user: 'Lionel Messi',
                costAction: 'Buy Config',
                costDetail: 'Purchased config',
                amountType: 'loss',
                amountValue: '$2',
                boughtLeft: '$999',
                date: '14 Feb, 10:10:55',
                dateIndex: 20200214101055
            },
            {
                user: 'Lionel Messi',
                costAction: 'Refil License',
                costDetail: 'Buy corporate License',
                amountType: 'profit',
                amountValue: '$1000',
                boughtLeft: '$1000',
                date: '13 Feb, 11:10:22',
                dateIndex: 20200213111022
            },


        ],
    });



    return (
        <div id="personal__cost__table" className="ustomStyle">
        <MaterialTable
            title="Editable Example"
            columns={state.columns}
            data={state.data}
            options={{
                draggable: false,
                showTitle: false,
                paging: false,
                tableLayout: 'fixed'
            }}
        />
        </div>
    );
}
export default CorporateCost;