import { licensesTypes } from './licenses.types';

const INITIAL_STATE = {
  licenses: {
      currentLicense: 'Corporate',
      currentTab: 'Activity',
      personalLicense: {
        isActivated: true,
        autoRenew: true,
        title: 'Personal License',
        daysLeft: '154',
        configsLeft: '324',
        balance: '1200.23'
      },
      corporateLicense: {
        isActivated: true,
        autoRenew: false,
        additionalScreen: false,
        title: 'Corporate License',
        daysLeft: '112',
        configsLeft: '301',
        balance: '1900.83'
      },
      options: {
        menuForChangingLicense: false
      }
  }
};


const licensesReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case licensesTypes.SET_CURRENT_LICENSE:
      return {
        ...state,
        licenses: {
          ...state.licenses,
          currentLicense: action.payload
        },
      };
      case licensesTypes.SET_CURRENT_TAB:
          return {
              ...state,
              licenses: {
                  ...state.licenses,
                  currentTab: action.payload
              },
          };

      case licensesTypes.SET_ADDITIONAL_SCREEN_CORPORATE:
          return {
              ...state,
              licenses: {
                  ...state.licenses,
                  corporateLicense: {
                      ...state.licenses.corporateLicense,
                      additionalScreen: action.payload
                  }
              },
          };
      case licensesTypes.SET_AUTO_RENEW_PERSONAL:
          return {
              ...state,
              licenses: {
                  ...state.licenses,
                  personalLicense: {
                      ...state.licenses.personalLicense,
                      autoRenew: action.payload
                  }
              },
          };
     case licensesTypes.SET_AUTO_RENEW_CORPORATE:
           return {
              ...state,
              licenses: {
                  ...state.licenses,
                  corporateLicense: {
                      ...state.licenses.corporateLicense,
                      autoRenew: action.payload
                  }
               },
            };



    case licensesTypes.SET_OPTIONS__LICENSE_MENU:
      return {
        ...state,
        licenses: {
          ...state.licenses,
          options: {
            ...state.licenses.options,
            menuForChangingLicense: action.payload
          }
         }
       };

    default:
      return state;
  }
};

export default licensesReducer;
