import React from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';

const NewTicket = () => {
    let tabs = [
        {name : 'Tickets', link: '/tickets'},
        {name : 'New Ticket', link: '/new-ticket'},
    ];

    return (
        <div id="dashboard-tickets">
            <TabsManager data={tabs} />
        </div>
    );
};

export default NewTicket;