import React from 'react';
import cogIcon from './cog.svg';

const DaysLeft = (props) => {
    return (
        <div className="tableCell-daysLeft">
            <div className="value">
            {props.dataValue}
            </div>
            <div className="days">
                365
            </div>
            <div className="btn">
                <img src={cogIcon} alt=""/>
            </div>
        </div>
    );
};

export default DaysLeft;