import React from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';
import MessageTable from './MessageTable/MessageTable'

const Tickets = () => {

    let tabs = [
        {name : 'Tickets', link: '/tickets'},
        {name : 'New Ticket', link: '/new-ticket'},
    ];
    let thead = [
        'Title', 'Last Message', 'Creation Date', 'Last Message'
    ];

    let tbody = [
        ['Lorem Ipsum', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '3 Apr, 18:11:33'],
        ['Test title', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '1 Apr, 10:33:45'],
        ['Lorem Ipsum Dolor...', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '2 Apr, 11:58:25'],
        ['Lorem Ipsum Dolor...', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '1 Apr, 08:34:11'],
        ['Lorem Ipsum Sit', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '2 Apr, 18:14:22'],
        ['Amet', 'Support: Lorem Ipsum dolor sit amet', '3 Apr, 18:54:45', '3 Apr, 21:35:45']
    ];

    return (
        <div id="dashboard-tickets">
            <TabsManager data={tabs} />

            <MessageTable tHead={thead} tBody={tbody} />
        </div>
    );
};

export default Tickets;