import { createMuiTheme } from "@material-ui/core/styles";

const primaryColor = '#2c2f38';
const secondaryColor = '#3d404a';

export default createMuiTheme({
    palette: {
        common: {
            primary: `${primaryColor}`,
            secondary: `${secondaryColor}`
        },
        primary: {
            main: `${primaryColor}`
        },
        secondary: {
            main: `${secondaryColor}`
        }
    }
})
