let usersTabClone, currentUserObj;

const usersReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case usersTypes.SET_COSTS:

            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesCosts = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };

        case usersTypes.SET_ACTIVITY:
            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesActivity = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };


        case usersTypes.SET_USERS:

            usersTabClone = [...state.usersTab];

            currentUserObj = usersTabClone.find((userObject) => {
                return userObject.user.id === action.payload.id;
            });

            if (currentUserObj) {
                currentUserObj.userOptions.seesUsers = action.payload.bool;
            }

            return {
                ...state,
                usersTab: usersTabClone
            };

        default:
            return state;
    }
};

export default usersReducer;
