import React from 'react';
import './style.scss';

const MessageTable = (props) => {
    let thead = props.tHead;
    let tbody = props.tBody


    let theadData = thead.map((theadItem, i) => {
        let tHeadItem;

        tHeadItem = (<td key={i.toString()}>{theadItem}</td>);

        return tHeadItem;
    });

    let tBodyData = tbody.map((tBodyItem, i) => {
        let tbodyItem, tBodyItemsHTML;

        tBodyItemsHTML = tBodyItem.map((item, x) => {
            return (<td key={x.toString()}>{item}</td>);
        })

        tbodyItem = (<tr key={i.toString()}>{tBodyItemsHTML}</tr>);

        return tbodyItem;
    });

    return (
        <div>
            <table className="table-message" rules="none">
                <thead>
                    <tr>
                        {theadData}
                    </tr>
                </thead>

                <tbody>
                    {tBodyData}
                </tbody>
            </table>
        </div>
    );
};

export default MessageTable;