import React from 'react';
import MaterialTable from 'material-table';
import './CorporateActivity__styles.scss';
import {connect} from "react-redux";

const CorporateActivity = () => {
    const [state, setState] = React.useState({

        columns: [
            { title: 'User', field: 'user', sorting: false },
            { title: 'Action', field: 'action', sorting: false },
            { title: 'Description', field: 'description',  sorting: false },
            { title: 'Date', field: 'date', customSort: (a, b) =>  a.dateIndex - b.dateIndex},
        ],
        data: [
            {
                user: 'Lionel Messi',  action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '20 Apr, 19:20:55',
                dateIndex: 20200420192055
            },
            {
                user: 'Lionel Messi',  action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '20 Apr, 19:12:33',
                dateIndex: 20200420191233
             },
            {
                user: 'Lionel Messi', action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '20 Apr, 19:10:55',
                dateIndex: 20200420191055
            },
            {
                user: 'Lionel Messi', action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '20 Feb, 13:13:55',
                dateIndex: 20200220131355
            },
            {
                user: 'Lionel Messi', action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '14 Feb, 10:10:55',
                dateIndex: 20200214101055
            },
            {
                user: 'Lionel Messi', action: 'Browser Authorization',
                description: 'Action detail here must be long or short',
                date: '13 Feb, 11:10:22',
                dateIndex: 20200213111022
            },


        ],
    });



    return (
        <div id="personal__activity__table" className="ustomStyle">
            <MaterialTable
                title="Editable Example"
                columns={state.columns}
                data={state.data}
                options={{
                    draggable: false,
                    showTitle: false,
                    paging: false,
                    tableLayout: 'fixed'
                }}
            />
        </div>
    );
}
export default CorporateActivity;