import React from 'react';

const UserProfile = (props) => {
    return (
        <div className="tableCell-profile">
            <img src={props.dataImage} alt="ALT"/>
            <div>
                <h5>{props.dataName}</h5>
                <p>{props.dataId}</p>
            </div>
        </div>
    );
};

export default UserProfile;