import React from 'react';
import './style.scss';

const dealsTable = (props) => {
    let thead = props.tHead;
    let tbody = props.tBody


    let theadData = thead.map((theadItem, i) => {
        let tHeadItem;

        tHeadItem = (<div key={i.toString()}>{theadItem}</div>);

        return tHeadItem;
    });

    let tBodyData = tbody.map((tBodyItem, i) => {
        let tbodyItem, tBodyItemsHTML;

        tBodyItemsHTML = tBodyItem.map((item, x) => {
                return (<div key={x.toString()}>{item}</div>);
        })

        tbodyItem = (<div className="tr" key={i.toString()}>{tBodyItemsHTML}</div>);

        return tbodyItem;
    });

    return (
        <div>
            <div className="table-deals table" >
                <div className="thead">
                    <div className="tr">
                        {theadData}
                    </div>
                </div>

                <div className="tbody">
                    {tBodyData}
                </div>
            </div>
        </div>
    );
};

export default dealsTable;