import {appTypes} from './app.types';

import icon_en from './icons-app-language/en.svg';
import icon_de from './icons-app-language/de.svg';
import icon_es from './icons-app-language/es.svg';
import icon_ch from './icons-app-language/ch.svg';
import icon_ru from './icons-app-language/ru.svg';

const INITIAL_STATE = {
    appState: {
        modals: {
            languageDropdown: false,
        },
        appLanguage: {
            currentAppLanguage: 'En',
            defaultAppLanguage: 'En',
            languagesInterface: [
                {value: 'En', title: 'English', icon: icon_en},
                {value: 'De', title: 'Deutch', icon: icon_de},
                {value: 'Es', title: 'Espaol', icon: icon_es},
                {value: 'Ch', title: '', icon: icon_ch},
                {value: 'Ru', title: '', icon: icon_ru}
            ]
        }
    }
};

const appReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case appTypes.SET_CURRENT_LANGUAGE:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    appLanguage: {
                        ...state.appState.appLanguage,
                        currentAppLanguage: action.payload

                    }
                }
            };

        case appTypes.SET_LANGUAGE_MODAL:
            return {
                ...state,
                appState: {
                    ...state.appState,
                    modals: {
                        ...state.appState.modals,
                        languageDropdown: action.payload
                    }
                }
            };

        default:
            return state;
    }
};

export default appReducer;
