import { userTypes } from './user.types';
import userPhoto from './../../Images/avatar.svg';

const INITIAL_STATE = {
  currentUser: {
    photo: userPhoto,
    name: 'Lionel Messi',
    id: 1234567890,
    userBalance: '999.45',
    creds: {
      login: 'messi',
      pass: '123456789'
    }
  },
};

const userReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {

    case userTypes.SET_CURRENT_USER:
      return {
        ...state
      };

    case userTypes.CHANGE_USER_NAME:
      return {
        ...state,
        currentUser: {
          ...state.currentUser,
          name: action.payload
        },
      };

    default:
      return state;
  }
};

export default userReducer;
